@import './variables.scss';

h2 {
  font-family: $font-family;

  padding: 5px;
  margin-left: 10px;
}

#navigation {
  padding: 4px;
  margin: 4px;
  align-self: flex-start;
  background-color: $primary-color;
  opacity: 0.5;
  position: fixed;
  z-index: 1;
  font-size: 1em;
  font-family: $font-family;
}

#navigationActive {
  padding: 4px;
  margin: 4px;
  align-self: flex-start;
  background-color: $primary-color;
  opacity: 0.8;
  height: 100vh;
  z-index: 1;
  position: fixed;
  margin: -5px;
  padding: 20px;
  font-size: 15px;
  font-family: $font-family;
  max-width: 70vw;
}

.navItem {
  margin: 5px;
  padding: 5px;
}

.navX {
  font-size: 53px;
}

#appTitle {
  margin-left: -35px;
}

.RecipeFilterClass {
  border-radius: 4px;
  padding: 2px;
  margin: 4px;
}